import React, { useState } from 'react'
import Toolbar from '../toolbar'

/* STYLE DO PRZEBUDOWANIA */
import '../../styles'

const App = () => {

    const [fontSize, setFontSize] = useState(100);

    return (
        <React.Fragment>
            <Toolbar fontSize={fontSize} onChangeFontSize={setFontSize} />
        </React.Fragment>
    )
};

export default App